import { useEffect, useState, useMemo } from "react";
import { Link } from "react-router-dom";

export default function Participants() {

  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [search, setSearch] = useState("");
  const [filterGroup, setFilterGroup] = useState("");
  const [filterStatus, setFilterStatus] = useState("");

  const sheetURL =
    "https://script.google.com/macros/s/AKfycbxeQeC3sQD9WUr5Ky_YVmcVoJC-iiUe_Y8mqqRoaGd-SiKaHx2G6xGbSIzFInhQ_G6A/exec?action=getAll";

  // ================= FETCH DATA WITH CACHE =================
  useEffect(() => {
    const CACHE_KEY = "participantsData";
    const CACHE_DURATION = 1 * 60 * 60 * 1000; // 1 

    // Cached data  check 
    const cachedData = localStorage.getItem(CACHE_KEY);
    if (cachedData) {
      const { data: cachedRows, timestamp } = JSON.parse(cachedData);
      const now = new Date().getTime();

      //  cache 1       use 
      if (now - timestamp < CACHE_DURATION) {
        setData(cachedRows);
        setLoading(false);
        return;
      }
    }

    // Cache expired    - Google Sheet  fetch 
    fetch(sheetURL)
      .then(res => res.json())
      .then(result => {
        if (result.success) {
          const dataToStore = {
            data: result.data,
            timestamp: new Date().getTime()
          };
          localStorage.setItem(CACHE_KEY, JSON.stringify(dataToStore));
          setData(result.data);
        }
        setLoading(false);
      })
      .catch(() => {
        //  API fail    cache use 
        if (cachedData) {
          const { data: cachedRows } = JSON.parse(cachedData);
          setData(cachedRows);
        }
        setLoading(false);
      });
  }, []);

  // ================= MASK HELPERS =================
  const maskPhone = (phone) => {
    if (!phone) return "";
    const str = phone.toString();
    return "XXXXXX" + str.slice(-4);
  };

   // ================= FILTER (useMemo) =================
  const filtered = useMemo(() => {
    return data.filter((item) => {

      const textMatch =
        item.name?.toLowerCase().includes(search.toLowerCase()) ||
        item.formNo?.toLowerCase().includes(search.toLowerCase()) ||
        String(item.phone || "").includes(search) ||
        String(item.aadhaar || "").includes(search);

      const groupMatch =
        filterGroup === "" || item.ageGroup === filterGroup;

      const statusMatch =
        filterStatus === "" || item.status === filterStatus;

      return textMatch && groupMatch && statusMatch;
    });
  }, [data, search, filterGroup, filterStatus]);

  // ================= EXPORT =================
  const exportExcel = () => {
    const table = document.getElementById("participantsTable").outerHTML;
    const blob = new Blob([table], { type: "application/vnd.ms-excel" });
    const url = URL.createObjectURL(blob);
    const a = document.createElement("a");
    a.href = url;
    a.download = "participants.xls";
    a.click();
  };

  const exportPDF = () => window.print();

  return (
    <div className="participant-box">

      <h1>Registered Participants</h1>
      <p>--   Quiz / Islamic Competition</p>

      <p style={{ color: "red" }}>
         :-          WhatsApp :
        <b> 94147 23722</b>
      </p>

      <p style={{ 
        color: "white", 
        background: "#2196F3", 
        padding: "12px", 
        borderRadius: "5px",
        marginBottom: "15px"
      }}>
         <b>:</b>      ,       <b>1  </b>       
      </p>
      <p style={{ 
        color: "white", 
        background: "#4ca51d", 
        padding: "12px", 
        borderRadius: "5px",
        marginBottom: "15px"
      }}>
         <b>:</b>            "Not Uploaded "    ,   
                       ,                          
      </p>

      {loading ? (
        <h3>Loading...</h3>
      ) : (
        <>
          {/* SEARCH */}
          <input
            className="search"
            placeholder="Search by Name / Form No / Phone / Aadhaar"
            onChange={(e) => setSearch(e.target.value)}
          />

          {/* FILTERS */}
          <select className="search" onChange={(e) => setFilterGroup(e.target.value)}>
            <option value="">All Groups</option>
            <option value="Group A">Group A</option>
            <option value="Group B">Group B</option>
            <option value="Group C">Group C</option>
            <option value="Group D">Group D</option>
          </select>

          <select className="search" onChange={(e) => setFilterStatus(e.target.value)}>
            <option value="">All Status</option>
            <option value="Paid">Paid</option>
            <option value="Pending">Pending</option>
          </select>

          {/* EXPORT */}
          <div style={{ marginTop: 10 }}>
            <button onClick={exportExcel}> Export Excel</button>
            <button onClick={exportPDF} style={{ marginLeft: 10 }}>
               Export PDF
            </button>
          </div>

          {/* TABLE */}
          <div className="table-area">
            <table id="participantsTable">
              <thead>
                <tr>
                  <th>Form No</th>
                  <th>Name</th>
                  <th>Father</th>
                  <th>Age</th>
                  <th>Address</th>
                  <th>Aadhaar</th>
                  <th>Phone</th>
                  <th>Group</th>
                  <th>Competition</th>
                  <th>Fees</th>
                  <th>Status</th>
                  <th>Photo</th>
                </tr>
              </thead>

              <tbody>
                {filtered.map((row, i) => {

                  //  PHOTO LOGIC (FINAL)
                  const photoUploaded =
                    (row.photo && row.photo.toString().trim() !== "") ||
                    row.DocsUploaded === "YES";

                  return (
                    <tr key={i}>
                      <td>{row.formNo}</td>
                      <td>{row.name}</td>
                      <td>{row.father}</td>
                      <td>{row.age}</td>
                      <td>{row.address}</td>
                      <td style={{width: "150px"}}>{row.aadhaar}</td>

                      <td>{maskPhone(row.phone)}</td>
                      <td>{row.ageGroup}</td>
                      <td>{row.competition}</td>
                      <td>{row.fees}</td>

                      <td
                        style={{
                          color: "white",
                          fontWeight: "bold",
                          background: row.status === "Paid" ? "green" : "red",
                          borderRadius: 5,
                          textAlign: "center"
                        }}
                      >
                        {row.status}
                      </td>

                      <td style={{ fontWeight: "bold", textAlign: "center" }}>
                        {photoUploaded ? (
                          <span style={{ color: "green" }}>
                            Photo Uploaded 
                          </span>
                        ) : (
                          <Link to="/upload-documents" style={{ color: "red" }}>
                            Not Uploaded 
                          </Link>
                        )}
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          </div>
        </>
      )}
    </div>
  );
}
