import { useState } from "react";

export default function Receipt() {

  const [formNo, setFormNo] = useState("");
  const [data, setData] = useState(null);
  const [error, setError] = useState("");

  const fetchReceipt = async () => {

    if(!formNo){
      alert("     ");
      return;
    }

    const url = `https://script.google.com/macros/s/AKfycbwG49ITACXn2-nCo1OAXuY1jjqiY-6BwJeV7kB9M1ArDMvXlcnBE3zFGF3JdezTt-Ko/exec?formNo=${formNo}`;

    const res = await fetch(url);
    const result = await res.json();

    if(result.success){
      setData(result);
      setError("");
    } else {
      setError("    ");
      setData(null);
    }
  }

  const printReceipt = () => {
    window.print();
  }

  return (
    <div className="registration-box">

      <h1>-- </h1>
      <h2>Receipt /  </h2>

      <input 
        placeholder=" Form Number  ( GRF-0005)"
        value={formNo}
        onChange={(e)=>setFormNo(e.target.value)}
      />

      <button onClick={fetchReceipt}>Receipt </button>

      {error && <p style={{color:"red"}}>{error}</p>}

      {data && (
        <div className="receipt">

          <h3> Registration Receipt</h3>

          <p><b> :</b> {data.formNo}</p>
          <p><b>:</b> {data.name}</p>
          <p><b>  :</b> {data.father}</p>
          <p><b>:</b> {data.phone}</p>
          <p><b>:</b> {data.competition}</p>

          <p>
            <b>Status:</b> 
            {data.status === "Paid" 
              ? <span style={{color:"green"}}> Paid</span> 
              : <span style={{color:"orange"}}> Pending</span>}
          </p>

          <button onClick={printReceipt}>Print / Download</button>

        </div>
      )}

    </div>
  );
}
